import React from 'react';
import elina from '../../img/elina.jpg';

const HomeScreen = () => (
  <div className="section">
    <div className="col-md-12 centered">
      <h2>Coming soon!</h2>
    </div>
    <div className="col-md-12 centered">
      <img src={elina} className="image" alt="appios" />
    </div>
  </div>
);

export default HomeScreen;



